  margin-top: 20px;
  border-radius: 10px;
  border: 1px solid #f2f2f2;
  background: #f2f2f2;
  padding: 20px;
}
.tishi{
  font-size: 12px;
  color: #ccc;
  margin-left: 20px;
}
textarea{
    width: 100%;
    height: 400px;
  }
</style>
